.about {
  height: 100%;
}
.about .about-me {
  margin: 0;
  position: absolute;
  width: 165px;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.about .about-cookie {
  margin: 0;
  position: absolute;
  width: 165px;
  top: 35%;
  left: 25%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.about .about-dog {
  position: fixed;
  width: 210px;
  top: 35%;
  left: 73%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.about .about-engineer {
  position: fixed;
  width: 205px;
  top: 79%;
  left: 25%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.about .about-bullet-journal {
  position: fixed;
  width: 190px;
  top: 79%;
  left: 73%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.about .about-line-top-right {
  position: fixed;
  width: 240px;
  top: 43%;
  left: 62%;
  z-index: -1;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
@media only screen and (max-width: 1040px) {
  .about .about-line-top-right {
    width: 200px;
  }
}
@media only screen and (min-width: 1441px) {
  .about .about-line-top-right {
    width: 300px;
  }
}
.about .about-line-top-left {
  position: fixed;
  width: 240px;
  top: 33%;
  left: 28.2%;
  z-index: -1;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  -ms-filter: fliph;
  /*IE*/
  -webkit-filter: fliph;
          filter: fliph;
  /*IE*/
